@import "../../utils/variables.scss";

.wrapper {
  display: flex;
  width: 100%;
  min-height: 100vh;
  background-color: #f5f5f5;
  box-shadow: inset 1px 0px 4px 2px rgba(0, 0, 0, 0.25);

  .selectField {
    margin-bottom: 2em;
  }

  .mainCard {
    display: flex;
    width: 100%;
    background-color: white;
    box-shadow: 0 1.5px 39px 0 rgba(0, 0, 0, 0.16);
    border-radius: 20px;
    padding-top: 55px;

    .left {
      width: 40%;
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    input[type="tel"] {
      width: 100%;
      border-radius: 10px;
      height: 50px;
      border-radius: 10px;
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1) !important;
      border: none;
      padding-left: 15px;
      margin-bottom: 1em;
    }

    .right {
      width: 55%;
      padding-top: 35px;
      padding-right: 2em;
      padding-left: 50px;

      .alreadyRegistered {
        display: flex;
        color: #7a86a1;
        justify-content: flex-end;
        align-items: flex-end;
        padding: 10px;
        top: 0;
        right: 0;
        color: #7a86a1;

        span {
          cursor: pointer;
          margin-left: 5px;
          color: #4b9bee;
        }
      }

      .welcome {
        .title {
          margin-top: 30px;
          margin-bottom: 40px;
          font-size: 30px;
          font-weight: 500;
        }
      }

      .logo {
        display: flex;
        flex-direction: column;
        align-items: center;

        .image {
          margin-bottom: 10px;

          img {
            width: 130px;
          }
        }
      }

      .error {
        width: 100%;
        color: red;
        font-size: 12px;
        margin-top: 3px;
        text-transform: capitalize;
      }

      .deptError {
        margin-bottom: 30px;
      }

      .usernameError {
        margin-top: -20px !important;
        margin-bottom: 10px;
        text-align: left;
      }

      .selectors {
        margin: 25px 0 35px 0;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        gap: 20px;

        .selectorPair {
          @media screen and (max-width: 1200px) {
            flex-direction: column;
          }

          display: flex;
          margin-bottom: 1px;

          &:last-child {
            .selector:last-child {
              margin-right: 0;
            }
          }
        }

        .selector {
          box-shadow: 0 1px 2px 0px rgba(0, 0, 0, 0.09);
          border-radius: 15px;
          padding: 12px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: start;
          border: 1px solid #ebedf0;

          .title {
            padding-left: 33px;
            margin-top: 5px;
            font-size: 13px;
            color: #575757;
          }

          .name {
            text-align: center;
            font-size: 15px;

            span {
              padding-top: 0;
              padding-bottom: 0;
            }
          }

          .checkbox {
            width: 50px;
            height: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            padding-bottom: 20px;
            padding-left: 5px;
            position: absolute;
            bottom: -15px;
            left: 50%;
            transform: translateX(-50%);
          }
        }

        .selector1 {
          box-shadow: 0 1px 2px 0 rgba(0, 215, 151, 1);
          border-radius: 15px;
          padding: 12px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: start;
          border: 1px solid #ebedf0;

          .title {
            padding-left: 33px;
            margin-top: 5px;
            font-size: 13px;
            color: #575757;
          }

          .name {

            text-align: center;
            font-size: 15px;

            span {
              padding-top: 0;
              padding-bottom: 0;
            }
          }

          .checkbox {
            width: 50px;
            height: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: -15px;
            left: 50%;
            transform: translateX(-50%);
          }
        }

        .selector2 {
          box-shadow: 0 1px 2px 0 rgba(145, 61, 237, 1);
          border-radius: 15px;
          padding: 12px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: start;
          border: 1px solid #ebedf0;

          .title {
            padding-left: 33px;
            margin-top: 5px;
            font-size: 13px;
            color: #575757;
          }

          .name {

            text-align: center;
            font-size: 15px;

            span {
              padding-top: 0;
              padding-bottom: 0;
            }
          }

          .checkbox {
            width: 50px;
            height: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            padding-left: 10px;
            position: absolute;
            bottom: -15px;
            left: 50%;
            transform: translateX(-50%);
          }
        }

        .selector3 {
          box-shadow: 0 1px 2px 0 rgba(237, 213, 61, 1);
          border-radius: 15px;
          padding: 12px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: start;
          border: 1px solid #ebedf0;

          .title {
            padding-left: 33px;
            margin-top: 5px;
            font-size: 13px;
            color: #575757;
          }

          .name {

            text-align: center;
            font-size: 15px;

            span {
              padding-top: 0;
              padding-bottom: 0;
            }
          }

          .checkbox {
            width: 50px;
            height: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            padding-left: 10px;
            position: absolute;
            bottom: -15px;
            left: 50%;
            transform: translateX(-50%);
          }
        }
      }

      form {
        * {
          user-select: none;
        }

        input[type="radio"] {
          width: 12px;
          height: 12px;
        }

        .errors {
          color: red;
          font-size: 13px;
          padding-left: 4px;
        }

        .textGroup {
          display: flex;
          gap: 20px;
          margin-top: 20px;
        }

        .agreeText {
          color: #575757;
          font-size: 14px;
          margin-right: 20px;
          margin-bottom: 30px;

          span {
            color: #4b9bee;
            cursor: pointer;
          }
        }

        .row {
          position: relative;

          svg {
            position: absolute;
            right: 15px;
            // top: 15x;
            bottom: 48px;
            width: 17px;
            cursor: pointer;
          }

          label {
            font-size: 0.9rem;
            margin-bottom: 0.5em;
            display: inline-block;
            font-weight: 400;
          }

          .firstName {
            margin-right: 10px;
          }

          .textField {
            flex: 1;
            margin-right: 30px;

            &:last-child {
              margin-right: 0;
            }

            input {
              width: 100%;
              border-radius: 10px;
              height: 50px;
              border-radius: 20px;
              border: 1px solid rgb(235, 235, 235);
              padding-left: 15px;
            }
          }
        }

        .row1 {
          position: relative;
          height: 110px;

          svg {
            position: absolute;
            right: 15px;
            color: #c0bdcc;
            width: 17px;
            top: 2.5em;
            cursor: pointer;
          }

          label {
            font-size: 0.9rem;
            margin-bottom: 0.5em;
            display: inline-block;
            font-weight: 500;
          }

          .firstName {
            margin-right: 10px;
          }

          .textField {
            flex: 1;
            margin-right: 30px;

            &:last-child {
              margin-right: 0;
            }

            input {
              width: 100%;
              border-radius: 10px;
              height: 50px;
              border-radius: 20px;
              border: 1px solid rgb(235, 235, 235);
              padding-left: 15px;
            }
          }
        }

        .checkboxContainer {
          margin: 20px 0;
        }

        .btnContainer {
          display: flex;
          justify-content: space-between;
          align-items: flex-end;

          .Button {
            box-shadow: 4px 22.6px 57px 0px rgba(54, 54, 54, 0.1);
          }

          div {
            color: #575757;
            font-size: 14px;
            margin-right: 20px;

            span {
              border-bottom: 1px solid #575757;
              color: $themePurple;
              font-weight: 500;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 2150px) {
  .wrapper {
    .mainCard {
      .right {
        width: 50%;
        padding-right: 1em;
      }

      .textField {
        width: 320px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .wrapper {
    .mainCard {
      .textField {
        width: 100%;
      }

      .left {
        width: 50%;
        // img {
        //   width: 490px;
        // }
      }

      .right {
        width: 50%;
        // padding-right: 1em;
      }

      .textGroup {
        display: flex;
        flex-direction: column;
      }
    }
  }
}

@media screen and (max-width: 1100px) {
  .wrapper {
    .mainCard {
      flex-direction: column;

      .textField {
        width: 100%;
      }

      .left,
      .right {
        width: 100%;
      }

      .left {
        padding-top: 50px;
      }

      .right {
        padding-top: 50px;
        width: 100% !important;
        padding-inline: 3em;
        max-width: unset;

        .selectors {
          width: 100%;

        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .wrapper {
    padding: 0;

    .mainCard {
      padding-bottom: 3em;

      .textField {
        width: 100%;
      }

      .left {
        padding-top: 0 !important;

        img {
          width: 100%;
        }
      }

      .right {
        padding-inline: 1em;

        .selectors {
          flex-direction: column;
          gap: 20px;

          .selectorPair {
            .selector {
              min-width: 105px;

              .name {
                padding-top: 0 !important;
              }

              .title {
                padding-top: 10px;
                justify-content: center;
              }
            }

            .selector:last-child {
              margin-right: 0;
            }
          }
        }

        form {
          .row {
            flex-direction: column;
          }
        }
      }
    }
  }
}