@import "../../utils/variables.scss";

.wrapper {
  display: flex;
  padding-top: calc(30px) !important;
  background-color: #f5f5f5;
  min-height: 100vh;
  justify-content: center;
  box-shadow: inset 1px 0px 4px 2px rgba(0, 0, 0, 0.25);

  .usernameError {
    margin-top: -20px !important;
    margin-bottom: 10px;
    text-align: left;
    font-size: 0.8rem;
    color: red;
    margin-top: -9px !important;
    margin-bottom: 10px;
  }

  .modalWrapper {
    width: 350px;
    min-height: 230px;
    padding: 1.5em;

    .title {
      display: flex;
      width: 100%;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 2.5em;

      div:first-child {
        font-weight: 500;
      }

      div:last-child {
        font-size: 0.9rem;
        background-color: rgba(1, 1, 1, 0.1);
        width: 25px;
        height: 25px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
      }
    }

    form {
      width: 100%;

      label {
        color: rgba(0, 0, 0, 1);
        font-size: 0.9rem;
        margin-bottom: 0.5em;
        display: inline-block;
      }

      input {
        width: 100%;
        border: 1px solid rgba(1, 1, 1, 0.1);
        height: 34px;
        border-radius: 5px;
        padding-left: 10px;
        margin-bottom: 0.5em;

        &::placeholder {
          color: rgba(1, 1, 1, 0.3);
        }

        &:focus {
          outline: none;
          box-shadow: 1px 1px 7px rgba(1, 1, 1, 0.2);
        }
      }

      p {
        font-size: 0.8rem;
        color: red;
        margin-bottom: 1em;
      }

      div.apiResponse {
        margin-top: 0.5em;
        font-size: 0.9rem;
      }

      div.apiResponse.success {
        color: rgb(0, 165, 0);
      }

      div.apiResponse.error {
        color: red;
      }
    }
  }

  .inner {
    display: flex;
    background-color: #f7f7f7;
    border-radius: 0px; //10px
    box-shadow: 0 1.5px 39px 0 rgba(0, 0, 0, 0.16);
    width: 100%;

    >div {
      width: 50%;
    }

    .left {
      display: flex;
      justify-content: center;
      width: 40%;
      align-items: center;
    }

    .right {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 7% 100px;

      .headerContainer {
        width: 100%;
        margin-bottom: 50px;
        display: flex;
        flex-direction: column;
        gap: 60px;

        .registerButton {
          display: flex;
          justify-content: end;
          color: #7a86a1;

          span {
            color: #4b9bee;
            cursor: pointer;
          }
        }

        .text {
          font-size: 30px;
          font-weight: 500;
        }
      }

      form {
        width: 100%;

        .textFieldGroup {
          display: flex;
          flex-direction: column;
          gap: 20px;
        }

        .row {
          position: relative;

          svg {
            position: absolute;
            right: 15px;
            top: 2.5em;
            width: 17px;
            color: $themePurple;
            cursor: pointer;
          }

          .label {
            font-weight: 400;
            position: relative;
            margin-bottom: 10px;
            display: block;
          }

          .textField {
            margin-bottom: 20px;

            input {
              width: 100%;
              border-radius: 10px;
              height: 50px;
              border-radius: 20px;
              border: 1px solid #ebebeb;
              padding-left: 15px;
            }
          }
        }

        .incorrectCreds {
          color: red;
          margin-bottom: 15px;
          font-size: 13px;
        }

        .checkboxContainer {
          display: flex;
          align-items: center;
          gap: 25px;

          label {
            margin-bottom: 0;
          }

          div {
            text-decoration: n;
            font-size: 0.8rem;
            color: #4b9bee;
            cursor: pointer;
          }
        }

        .btnContainer {
          margin-top: 30px;

          div {
            color: #575757;
            font-size: 14px;
            padding-top: 50px;
            margin-right: 20px;

            span {
              border-bottom: 1px solid #575757;
              color: $themePurple;
              font-weight: 500;
              cursor: pointer;
            }
          }

          Button {
            border-radius: 20px !important;
            box-shadow: 3.99px 22.65px 57px 0px #36363642;
          }

          display: flex;
          justify-content: space-between;
          align-items: flex-end;
        }
      }
    }
  }
}

@media screen and (max-width: 2150px) {
  .wrapper {
    .inner {
      .right {
        padding: 0 5%;
        margin-top: 60px;
      }

      .textField {
        width: 320px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .wrapper {
    .inner {
      .right {
        padding: 0 35px;
        margin-top: 60px;
      }

      .textField {
        width: 280px;
      }
    }
  }
}

@media screen and (max-width: 1000px) {
  .wrapper {
    .inner {
      .left {
        margin-top: 100px;
        img {
          // width: 350px;
        }
      }

      .textFieldGroup {
        flex-direction: column;
      }

      .textField {
        width: 100%;
      }
    }
  }
}

@media screen and (max-width: 850px) {
  .wrapper {
    .inner {
      flex-direction: column !important;
      padding-bottom: 50px;
      padding-top: 5px;

      .registerButton {
        position: relative;
        bottom: 60px;
      }

      >div {
        width: 100%;
      }

      .left {
        width: 100%;
      }

      .right {
        padding-top: 40px;

        .headerContainer {
          .text {
            position: absolute;
          }

          .subtext {
            padding-top: 10px;
          }
        }

        .logoContainer {
          img {
            display: none;
          }
        }
      }
    }
  }
}