@import "../../utils/variables.scss";

.wrapper {
  display: flex;
  padding-top: calc(30px) !important; //#{$navbarHeight} +
  background-color: #f5f5f5;
  min-height: 100vh;
  justify-content: center;

  .usernameError {
    margin-top: -20px !important;
    margin-bottom: 10px;
    text-align: left;
    font-size: 0.8rem;
    color: red;
    margin-top: -9px !important;
    margin-bottom: 10px;
  }

  .modalWrapper {
    width: 350px;
    min-height: 230px;
    padding: 1.5em;

    .title {
      display: flex;
      width: 100%;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 2.5em;

      div:first-child {
        font-weight: 500;
      }

      div:last-child {
        font-size: 0.9rem;
        background-color: rgba(1, 1, 1, 0.1);
        width: 25px;
        height: 25px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
      }
    }

    form {
      width: 100%;

      label {
        color: rgba(1, 1, 1, 0.6);
        font-size: 0.9rem;
        margin-bottom: 0.5em;
        display: inline-block;
        font-family: 'Outfit', sans-serif;
      }

      input {
        width: 100%;
        border: 1px solid rgba(1, 1, 1, 0.1);
        height: 34px;
        border-radius: 5px;
        padding-left: 10px;
        margin-bottom: 0.5em;

        &::placeholder {
          color: rgba(1, 1, 1, 0.3);
        }

        &:focus {
          outline: none;
          box-shadow: 1px 1px 7px rgba(1, 1, 1, 0.2);
        }
      }

      p {
        font-size: 0.8rem;
        color: red;
        margin-bottom: 1em;
      }

      div.apiResponse {
        margin-top: 0.5em;
        font-size: 0.9rem;
      }

      div.apiResponse.success {
        color: rgb(0, 165, 0);
      }

      div.apiResponse.error {
        color: red;
      }
    }
  }

  .inner {
    display: flex;
    background-color: white;
    border-radius: 0px; //10px
    box-shadow: 0 1.5px 39px 0 rgba(0, 0, 0, 0.16);
    width: 100%;

    >div {
      width: 50%;
    }

    .left {
      display: flex;
      justify-content: center;
      width: 40%;
      align-items: center;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .right {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 7% 100px;

      .loginButton {
        display: flex;
        justify-content: end;
        color: #7a86a1;

        span {
          color: #4b9bee;
          cursor: pointer;
        }
      }

      .headerContainer {
        width: 100%;
        margin-bottom: 40px;
        display: flex;
        flex-direction: column;
        gap: 10px;

        .text {
          font-size: 30px;
          font-weight: 500;
          margin-top: 40px;
        }

        .subtext {
          color: rgba(1, 1, 1, 0.5);
          margin-bottom: 10px;
        }
      }

      form {
        width: 100%;

        .row {
          position: relative;

          .label {
            font-weight: 500;
            position: relative;
            color: black;
            display: block;
            margin-bottom: 10px;
            font-family: "Outfit", sans-serif;
          }

          .textField {
            margin-bottom: 20px;

            input {
              width: 100%;
              border-radius: 10px;
              height: 50px;
              border-radius: 20px;
              border: 1px solid #ebebeb;
              padding-left: 15px;
            }
          }
        }

        .incorrectCreds {
          color: red;
          margin-bottom: 15px;
        }

        .btnContainer {
          margin-top: 30px;

          div {
            color: #575757;
            font-size: 14px;
            padding-top: 50px;
            margin-right: 20px;

            span {
              border-bottom: 1px solid #575757;
              color: $themePurple;
              font-weight: 500;
              cursor: pointer;
            }
          }

          Button {
            border-radius: 20px !important;
            box-shadow: 3.99399995803833px 22.650999069213867px 57px 0px #36363642;
          }

          display: flex;
          justify-content: space-between;
          align-items: flex-end;
        }
      }
    }
  }
}

@media screen and (max-width: 2180px) {
  .wrapper {
    .inner {
      .right {
        padding: 60px 5%;
      }

      .textField {
        width: 300px;
      }

      .registerButton {
        position: relative;
        bottom: 30px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .wrapper {
    .inner {
      .right {
        padding: 0 35px;
        margin-top: 60px;
      }

      .textField {
        width: 300px;
      }
    }
  }
}

@media screen and (max-width: 1000px) {
  .wrapper {
    .inner {
      .left {
        img {
          width: 350px;
        }
      }

      .textFieldGroup {
        flex-direction: column;
      }

      .textField {
        width: 300px;
      }
    }
  }
}

@media screen and (max-width: 850px) {
  .wrapper {
    .inner {
      flex-direction: column !important;
      padding-bottom: 50px;
      padding-top: 5px;

      .loginButton {
        position: relative;
        bottom: 60px;
      }

      .textField {
        width: 100%;
      }

      >div {
        width: 100%;
      }

      .left {
        margin: auto;
        width: 100%;

        img {
          width: 100%;
        }
      }

      .right {
        padding-top: 40px;

        .headerContainer {}

        .logoContainer {
          img {
            display: none;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .loginButton {}
}