.wrapper {
  width: 100%;
  height: 100vh;
  padding-top: 125px;
  background-color: #f2f2f2;
  display: flex;
  justify-content: center;
  .loaderWrapper {
    width: 100%;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .card {
    width: 900px;
    height: 500px;
    padding: 40px;
    padding-left: 50px;
    border-radius: 25px;
    background-color: white;
    box-shadow: 0 0 50px 0 #d9d9d9 !important;
    form {
      width: 100%;
      margin-bottom: 0px;
      .header {
        margin-bottom: 20px;
        div:first-child {
          margin-bottom: 10px;
          font-size: 20px;
          font-weight: 500;
        }

        div:nth-child(2) {
          margin-bottom: 30px;
          color: #949494;
        }
      }

      .row {
        display: flex;
        > div {
          width: 50%;
        }

        div:first-child {
          margin-right: 20px;
        }
      }

      div.textField,
      div.selectField {
        margin-bottom: 15px;
      }

      label {
        display: inline-block;
        margin-bottom: 8px;
        font-size: 14px;
      }

      div.textField {
        input {
          width: 100%;
          border-radius: 15px !important;
          height: 50px;
          border-radius: 10px;
          box-shadow: 0 0 6px 0 rgba(0, 0, 0, 0.1) !important;
          border: none;
          padding-left: 20px;
          &::placeholder {
            color: #a6a6a6;
          }
        }
      }

      button {
        margin-top: 25px;
        border-radius: 10px !important;
        width: 100px !important;
      }

      .error {
        color: red;
        font-size: 13px;
        margin-top: 5px;
        padding-left: 2px;
      }
    }

    .oldPassError {
      color: red;
    }
  }
}

@media screen and (max-width: 920px) {
  .wrapper {
    .card {
      width: 95%;
    }
  }
}

@media screen and (max-width: 750px) {
  .wrapper {
    .card {
      padding: 30px;
    }
  }
}

@media screen and (max-width: 600px) {
  .wrapper {
    .card {
      padding: 20px;
      height: fit-content;
      form {
        .row {
          flex-direction: column;
          > div {
            width: 100%;
          }
        }

        .textField {
          margin-right: 0 !important;
        }

        .selectField {
          width: 100%;
        }
      }
    }
  }
}
